"use client";

import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import Card from '../../components/Card';
import Button from '../../components/Button';
import FormField from '../../components/FormField';
import Spinner from '../../components/Spinner';
import Alert from '../../components/Alert';

export default function PatientLogin() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);
  const router = useRouter();

  const validate = () => {
    if (!email || !email.includes('@')) return 'Please enter a valid email';
    if (!password || password.length < 6) return 'Password must be at least 6 characters';
    return null;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const clientError = validate();
    if (clientError) return setError(clientError);
    setError('');
    setLoading(true);

    try {
      const response = await fetch('/api/auth/patient/login', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password }),
      });

      const data = await response.json();

      if (!response.ok) {
        setError(data.error || 'Login failed');
        return;
      }

      localStorage.setItem('token', data.token);
      localStorage.setItem('user', JSON.stringify(data.patient));
      localStorage.setItem('userRole', 'patient');

      router.push('/patient/dashboard');
    } catch (err: any) {
      setError(err.message || 'An error occurred');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="max-w-md mx-auto mt-12">
      <Card>
        <h1 className="text-2xl font-bold mb-4">Patient Login</h1>

        {error && <Alert type="error">{error}</Alert>}

        <form onSubmit={handleSubmit} className="mt-4">
          <FormField label="Email" htmlFor="email">
            <input
              id="email"
              type="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="w-full px-3 py-2 rounded border border-gray-200 focus:ring-2 focus:ring-blue-200"
            />
          </FormField>

          <FormField label="Password" htmlFor="password">
            <input
              id="password"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="w-full px-3 py-2 rounded border border-gray-200 focus:ring-2 focus:ring-blue-200"
            />
          </FormField>

          <div className="mt-4">
            <Button type="submit" className="w-full" disabled={loading}>
              {loading ? <Spinner className="h-4 w-4" /> : 'Login'}
            </Button>
          </div>
        </form>

        <p className="mt-4 text-center text-sm text-gray-600">
          Don't have an account? <Link href="/register" className="text-blue-600 underline">Register</Link>
        </p>

        <p className="mt-2 text-center text-sm">
          <Link href="/doctor/login" className="text-blue-600 underline">Doctor login</Link>
        </p>
      </Card>
    </div>
  );
}
